<template>
  <section class="step-4">
    <div class="add-place-des">
      <h4>Place Type</h4>
    </div>
    <ValidationObserver v-slot="{ invalid }">
      <form @submit.prevent="" class="form">
        <ValidationProvider
          name="roomsNum"
          rules="required|numeric"
          v-slot="{ errors }"
        >
          <div class="form-input-wrapper">
            <label for="">Number of rooms</label>
            <div class="form-input-element">
              <input v-model="roomsNum" type="number" />
            </div>

            <span class="form-error">{{ errors[0] }}</span>
          </div>
        </ValidationProvider>

        <ValidationProvider
          name="familiesNum"
          rules="required|numeric"
          v-slot="{ errors }"
        >
          <div class="form-input-wrapper">
            <label for="">Number of families</label>
            <div class="form-input-element">
              <input v-model="familiesNum" type="number" />
            </div>

            <span class="form-error">{{ errors[0] }}</span>
          </div>
        </ValidationProvider>

        <ValidationProvider
          name="bathsNum"
          rules="required|numeric"
          v-slot="{ errors }"
        >
          <div class="form-input-wrapper">
            <label for="">Number of baths</label>
            <div class="form-input-element">
              <input v-model="bathsNum" type="number" />
            </div>

            <span class="form-error">{{ errors[0] }}</span>
          </div>
        </ValidationProvider>

        <Navigation :valid="!invalid" />
      </form>
    </ValidationObserver>
  </section>
</template>

<script>
import Navigation from "./Navigation.vue";
import { ValidationObserver, ValidationProvider, extend } from "vee-validate";
import { numeric, required } from "vee-validate/dist/rules";

extend("required", {
  validate(value) {
    return {
      required: true,
      valid: ["", " ", null, undefined].indexOf(value) === -1,
      message: "This field is required",
    };
  },
  computesRequired: true,
});

extend("numeric", {
  ...numeric,
  message: "This field only accept numbers",
});

export default {
  components: { Navigation, ValidationObserver, ValidationProvider, extend },
  data() {
    return {};
  },
  methods: {},
  computed: {
    roomsNum: {
      get() {
        return this.$store.state.steps.step4.roomsNum;
      },
      set(value) {
        this.$store.commit("setRoomsNum", { roomsNum: value });
      },
    },
    familiesNum: {
      get(){
        return this.$store.state.steps.step4.familiesNum
      },
      set(value){
        this.$store.commit("setFamiliesNum", { familiesNum: value })
      }
    },
    bathsNum: {
      get(){
        return this.$store.state.steps.step4.bathsNum
      },
      set(value){
        this.$store.commit("setBathsNum", { bathsNum: value })
      }
    }
  },
};
</script>

<style scoped lang="scss"></style>
